<template>
<div>
  <el-row style="text-align:center;">
    <el-input
      style="width:90%;margin-top:50px"
      class="input-class"
      type="textarea"
      :rows="12"
      placeholder=""
      v-model="textarea">
    </el-input>
  </el-row>
  <el-row style="text-align:center">
    <el-button type="primary" icon="el-icon-edit" v-if=role></el-button>
  </el-row>
</div>
</template>

<style>

</style>

<script>
  export default {
    data() {
      return {
        role:false,
        textarea: '27315363061730'
      }
    },
    mounted:function(){
      if(sessionStorage.getItem('role') == 'false'){
        this.role = false;
      }else{
        this.role = true;
      }
    }
  };
</script>




